:root {
  --gold: #daa520;
  --black: #000000;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

ul {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 30px;
  background: var(--black);
  color: var(--gold);
}

a {
  color: var(--gold) !important;
  text-decoration: none;
}

.container {
  list-style: none;
  min-height: 100px;
}

.navButton {
  width: 20%;
  height: 150px;
  border-style: dashed dashed solid dashed;
}

.face {
  max-width: 300px;
  min-width: 300px;
}

.aboutText {
  font-size: 25px;
}

.firstWorkButton {
  max-height: 300px;
}

.firstWorkButton img {
  max-height: 300px;
}

.contactButton {
  height: 150px;
  width: 25%;
  font-size: 20px;
}

.name {
  width: 75%;
  font-size: 75px;
  height: 250px;
  background: linear-gradient(
    45deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(218, 165, 32, 1) 100%
  );
}

.firstWorkText {
  height: 300px;
  font-size: 30px;
  margin: 10px;
  background-color: white;
}

.workText {
  font-size: 30px;
  margin: 10px;
  background-color: white;
}

@media only screen and (max-width: 800px) {
  .navButton {
    width: 40%;
  }
}

@media only screen and (max-width: 600px) {
  .navButton {
    width: 90%;
  }

  .contactButton {
    font-size: 15px;
  }
}
